html,
body,
#root,
.App {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.App {
  background: #005500;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.title {
  position: relative;
  width: calc(100% - 42px);
  padding-left: 42px;
  color: white;
  font-weight: 600;
  font-size: 300%;
  margin-bottom: 24px;
  border-bottom: solid #002200 4px;
  display: -ms-flexbox;
  display: flex;
  overflow-x: auto;
}

.title button {
  margin: 5px;
  background: white;
  border-radius: 5px;
  -webkit-box-shadow: 0px 0px 6px 0px #111;
          box-shadow: 0px 0px 6px 0px #111;
}

button:focus {
  outline: 0;
}

button:active {
  background: #dddddd;
}

#title {
  -ms-flex: 1 1;
      flex: 1 1;
}

#table {
  width: 100%;
  overflow: auto;
  position: relative;
  -ms-flex: 1 1;
      flex: 1 1;
}

.modal button {
  display: block;
  width: 100%;
  font-size: 14px;
  height: 32px;
  margin-top: 24px;
  border-radius: 6px;
  border: none;
  -webkit-box-shadow: 0px 0px 8px 0px #111;
          box-shadow: 0px 0px 8px 0px #111;
}

.modal button:hover {
  background: #009900;
}

.modal button:active {
  -webkit-box-shadow: 0px 0px 0px 0px #111;
          box-shadow: 0px 0px 0px 0px #111;
  background: #ffffff;
}

.card {
  position: absolute;
  width: 120px;
  margin-right: 5px;
  border-radius: 10px;
  -webkit-box-shadow: 0px 0px 4px 0px #111;
          box-shadow: 0px 0px 4px 0px #111;
}

#input {
  position: relative;
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  height: 100px;
}

.modal {
  position: absolute;
  width: 400px;
  max-width: calc(100% - 84px);
  padding: 42px;
  background: #eef6eee0;
  margin: auto;
  border-radius: 25px;
  -ms-flex-item-align: center;
      align-self: center;
  margin-top: 42px;
  -webkit-box-shadow: 0px 0px 20px 0px #111;
          box-shadow: 0px 0px 20px 0px #111;
  font-size: 24px;
  text-align: left;
  overflow: auto;
  max-height: calc(100% - 168px);
}

.rule-button {
  font-size: 18px;
  padding: 8px;
  border-radius: 4px;
  margin-bottom: 12px;
  -webkit-box-shadow: 0px 0px 5px 0px #111;
          box-shadow: 0px 0px 5px 0px #111;
  background: #ddffdd;
  cursor: pointer;
}

.rule-button:hover {
  background: #99ee99;
}

.rule-button:active {
  background: white;
  -webkit-box-shadow: 0px 0px 0px 0px #111;
          box-shadow: 0px 0px 0px 0px #111;
}

.description {
  font-size: 16px;
}

.custom-rule {
  width: 100%;
  height: 200px;
}

.custom-solution {
  width: 100%;
  height: 24px;
}



